@charset "UTF-8";
:root {
  --font-family: "Inclusive Sans", sans-serif;
}

:root {
  --bittersweet-shimmer: hsla(355, 49%, 52%, 1);
  --maize: hsla(53, 98%, 65%, 1);
  --yellow-green: hsla(79, 54%, 51%, 1);
  --ghost-white: hsla(240, 100%, 99%, 1);
  --rich-black: hsla(203, 69%, 5%, 1);
  --light: #ffcc70;
  --lightest: #fffadd;
  --medium: #8ecddd;
  --darkest: #22668d;
}

html {
  font-family: var(--font-family);
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.bars-container {
  transform: scaleX(-1) rotate(180deg);
  margin-top: 0.625rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  height: 100vh;
  width: 100%;
  padding: 0 1.25rem 0 1.25rem;
}

.bar {
  transition: height 0.3s ease-out;
  margin: 0 0.0625rem;
  background-color: #22668d;
}

.created {
  transition: all 1s ease-in-out;
}

.navbar {
  width: 100%;
}
.navbar__wrapper {
  background-color: var(--darkest);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 11px 0px;
  padding-block: 0.625rem;
  /* Tablet */
  /* Pc */
}
@media (min-width: 40em) {
  .navbar__wrapper {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0.625rem;
  }
}
@media (min-width: 71.875em) {
  .navbar__wrapper {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0.625rem;
    background-color: var(--darkest);
  }
}
.navbar-title {
  /* Tablet */
  /* Pc */
  color: var(--lightest);
}
@media (min-width: 40em) {
  .navbar-title {
    max-width: 7.8125rem;
  }
}
@media (min-width: 71.875em) {
  .navbar-title {
    max-width: 100%;
  }
}
.navbar-algorithm .select-wrapper {
  position: relative;
  display: inline-block;
}
.navbar-algorithm .select-wrapper:after {
  content: "â–¼";
  position: absolute;
  top: 50%;
  right: 0.625rem;
  transform: translateY(-50%);
  pointer-events: none;
  color: var(--lightest);
}
.navbar-algorithm .select-wrapper select {
  font-size: 1rem;
  appearance: none;
  width: 12.5rem;
  background: var(--darkest);
  padding: 0.625rem;
  color: var(--lightest);
  border: 0.0625rem solid var(--light);
  border-radius: 0.25rem;
  cursor: pointer;
  /* Tablet */
  /* Pc */
}
@media (min-width: 40em) {
  .navbar-algorithm .select-wrapper select {
    max-width: 7.8125rem;
  }
}
@media (min-width: 71.875em) {
  .navbar-algorithm .select-wrapper select {
    max-width: 100%;
  }
}
.navbar-array-size {
  display: flex;
  flex-direction: column;
  gap: 5px;
  /* Tablet */
  /* Pc */
  /* Tablet */
}
@media (min-width: 40em) {
  .navbar-array-size {
    max-width: 7.8125rem;
  }
}
@media (min-width: 71.875em) {
  .navbar-array-size {
    max-width: 100%;
  }
}
.navbar-array-size label {
  color: var(--lightest);
  margin-right: 10px;
}
.navbar-array-size input[type=range] {
  outline: none;
  height: 0.1875rem;
  color: inherit;
  margin-right: 1em;
  background-color: var(--lightest);
  cursor: pointer;
  padding: 0;
}
@media (min-width: 40em) {
  .navbar-array-size {
    display: block;
  }
}
.navbar-buttons button {
  font-size: 0.9375rem;
  margin-left: 0.0625rem;
  padding: 0.625rem 1.25rem;
  color: var(--lightest);
  background-color: var(--light);
  border: none;
  border-radius: 0.25rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.navbar-buttons button:hover {
  background-color: var(--medium);
}
.navbar-buttons button:first-child {
  margin-left: 0;
}

body {
  background-color: var(--ghost-white);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  height: 100vh;
}